42;\n        }\n       \n        .counter { \n            display: flex;\n            justify-content: center;\n            align-items: center;\n            @include border-radius(35%);\n            background-color: $primary-brand;\n            color: #fff;\n            margin-top: -0.35em;\n            margin-left: -0.2em;\n            position: absolute;\n            text-align: center;\n            min-width: 2em;\n            left: 2em;\n            line-height: 1.4;\n            height: 1.4em;\n            font-size: 0.8em;\n            @include breakpoint(phablet) {\n                height: 1.5em;\n                min-width: 2em;\n                line-height: 1.5;\n                font-size: .8em;\n            }\n            &.hidden {\n                display: none;\n            }\n        }\n        .download-icon {\n            svg {\n                width: 35px;\n                height: 22px;\n               \n                @include breakpoint(phablet) {\n                    width: 42px;\n                    height: 27px;\n                }\n                \n                &:hover {\n                color: #1073BC;\n                cursor: pointer;\n            }\n            }\n            \n        }\n        \n    }\n    .download-item-list-wrapper {\n        position: absolute;\n        .download-item-list-container {\n            @include border-radius(0 0 3px 3px);\n            @include transition(all 0.1s ease-in-out);\n            box-sizing: border-box;\n            overflow: hidden;\n            max-height: 0;\n            position: relative;\n            top: 10px;\n            background-color: #fbfcfd;\n            right: 205px;\n            width: 260px;\n            @include breakpoint(phablet) {\n                right: 245px;\n                width: 300px;\n            }\n            @media print {\n                display: none;\n            }\n            &.open {\n                @include box-shadow(\n                    0 5px 5px 